.contact-form {
  max-width: 400px;
  margin: 0 auto;
  padding: 20px; 
  box-sizing: border-box; 
}

.form-group {
  margin-bottom: 20px;
}

label {
  display: block;
  margin-bottom: 5px;
}

input[type="text"],
input[type="email"],
textarea {
  width: 100%;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  box-sizing: border-box; 
}

textarea {
  height: 150px;
}

button {
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
  cursor: pointer;
}

button:hover {
  background-color: #0056b3;
}

@media only screen and (max-width: 600px) {
  .contact-form {
    max-width: 100%;
  }
  
  textarea {
    height: 100px; 
  }
}

@media only screen and (min-width: 1024px) {
  .contact-form {
    max-width: 600px;
  }
}
